
#home {
    height: 100%;
    color:#666;
    background-color:#f4f4f8;
}
.search {
    height: 44px;
    background: white;
    padding-top: 1px;
    position:relative;
}
.search>span{
    width:28px;
    height: 28px;
    position:absolute;
    right: .45rem;
    top: .35rem;
    background: url(../assets/fenlei.png)no-repeat;
    background-size:28px 28px;
    display: inline-block;
}
.search_box {
    height: 28px;
    line-height: 28px;
    margin-top: 10px;
    border-radius: .17067rem;
    color: #666;
    margin-right: 1.87733rem;
    margin-left: .59733rem;
    font-size: 12px;
    background-color: #f2f2f2;
}
.search_box>.icon{
    width: 15px;
    height: 15px;
    display: inline-block;
    vertical-align: top;
    background: url(../assets/serach.png)no-repeat;
    background-size:15px 15px;
    vertical-align: top;
    margin-top:6px;
    margin-right:5px;
    margin-left:5px;
}
.swiper-container {
    width: 100%;
    font-size: 0;
    /* position: relative;
    z-index: 1000; */
    overflow: hidden;
}
.swiper-wrapper {
    /* font-size: 0; */
    width: 700% !important;
}
.swiper-slide{
    float: left !important;
}
.swiper-container img {
    width: 100%;
    height: auto;
}
.common-floor {
    margin-bottom: .5rem;
}
.multi-block1 {
    background: white;
    display: -webkit-flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.multi-block1>a {
    display: inline-block;
    margin-top: .4rem;
    margin-bottom: .5rem;
}
.multi-block1>a>img {
    width: 40px;
    display: block;
    margin: 0 auto .42667rem;
}
.multi-block1>a>p {
    font-size: 12px;
    text-align: center;
    color: #666;
}
.multi-block2 a {
    display: block;
}
.multi-block2 img {
    box-sizing: border-box;
}
.multi-block2>a {
    width: 50%;
    float: left;
}
.multi-block2>a>img {
    width: 100%;
    border-right: 1px solid #f2f2f2
}
.multi-block2>div {
    float: left;
    width: 50%;
}
.multi-block2>div img {
    width: 100%
}
.multi-block2>div img:first-of-type {
    border-bottom: 1px solid #f2f2f2;
    border-right: 1px solid #f2f2f2;
}
.multi-block3>a {
    display: block;
}
.multi-block3>a>img {
    width: 100%;
}
.back-top>.slide-box{
    bottom: 3rem;
}




