
body{
    background: #fff!important;
}
a{
    display: inline-block;
}
.classify{
    width: 100%;
}
.classify a{
    width: 50%;
}
.classify img{
    width: 100%;
}
.goods>li>a>img{
    width: 100%;
}
.banner{
    height: 7.5rem;
}
.list{
    padding-top: 0.7rem;
    height: 8rem;
    white-space: nowrap;
    width: auto;
    overflow: hidden;
    overflow-x: auto;
}
.good{
    display: inline-block;
    height: 7.5rem;
    width: 4.5rem;
    margin-left: 0.5rem;
}
.good a{
    height: 7.5rem;
    width: 100%;
}
.good img{
    height: 4.5rem;
    width: 4.5rem;
}
.price{
    text-align: center;
}
.delp{
    color: #bbb;
    text-decoration: line-through;
}
.goodName{
    white-space: normal;
    text-align: center;
}



